@import "../../assets/css/base/styles";
.pagination {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .paginationItem {
    background: #fff;
    border: 2px solid #666;
    padding: 10px 15px;
    @include prefix(border-radius, 50%);
    height: 45px;
    width: 45px;
    position: relative;
    margin: 0 5px;
    cursor: pointer;
  }
  
  .paginationItem span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .prev,
  .next {
    background: transparent;
    border: none;
    padding: 10px;
    color: $color-active;
    @include prefix(box-shadow, 0 0 3px rgba($color-active, 0.4));
    margin: 0 10px;
    cursor: pointer;
    font-weight: bold;
    @include prefix(border-radius, 7px);
  }
  
  .paginationItem.active {
    border: 1px solid $color-active;
    color: $color-active;
    background-color: #ebc4a5;
    pointer-events: none;
  }
  
  .prev.disabled,
  .next.disabled {
    pointer-events: none;
    box-shadow: none;
    color: #999;
  }